.movierow{
    margin-bottom: 30px;
}
.movierow h2{
    margin: 0px 0px 0px 30px;
}
.movierow--listarea{
    overflow-x: hidden;
    padding-left: 30px;
}
.movierow--list{
    transition: all ease 0.5s;
}
.movierow--item{
display: inline-block;
width: 150px;
cursor: pointer;
}
.movierow--item img{
    width: 100%;
    transform: scale(0.9);/*serve para que a imagem diminua um pouco mesmo estando 100% de tamanho,
     serve para criar o efeito de estar com espa√ßamento entre uma e outra*/
     transition: all ease 0.2s ;

}
.movierow--item img:hover{
    transform: scale(1);

}

.movieRow--left,
.movieRow--right{
    position: absolute;
    width: 40px;
    height: 225px;
    background-color: rgba(0,0,0,0.6);
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease 0.5s;
}
.movieRow--left{
    left: 0;
}
.movieRow--right{
    right: 0;
}

.movierow:hover .movieRow--left,
.movierow:hover .movieRow--right{
    opacity: 1;
}
@media (max-width:760) {
    .movieRow--left,
    .movieRow--right{
        opacity: 1;
    }
}